import "./join.css";

export const JoinBusiness = (props) => {
  return (
    <div className="join">
      <div className="joinInput">
        <img
          className="img"
          src={require("./camomileBusiness.png").default}
        ></img>
        <h2 className="inputTitle"> 아이디 </h2>
        <input
          className="inputBox"
          name="id"
          id="id"
          placeholder="아이디를 입력하세요"
        ></input>

        <h2 className="inputTitle"> 비밀번호 </h2>
        <input
          className="inputBox"
          name="pw"
          id="pw"
          placeholder="비밀번호를 입력하세요"
          type={"password"}
        ></input>
        <h2 className="inputTitle"> 비밀번호 확인 </h2>
        <input
          className="inputBox"
          name="pwConfirm"
          id="pwConfirm"
          placeholder="비밀번호를 확인하세요"
          type={"password"}
        ></input>

        <h2 className="inputTitle"> 카페명 </h2>
        <input
          className="inputBox"
          name="cafeName"
          id="cafeName"
          placeholder="카페명을 입력하세요"
        ></input>

        <h2 className="inputTitle"> 카페 연락처 </h2>
        <input
          className="inputBox"
          name="cafeNum"
          id="cafeNum"
          placeholder="카페 전화번호를 입력하세요"
        ></input>

        <h2 className="inputTitle"> 카페 주소 </h2>
        <input
          className="inputBox"
          name="cafeAdd"
          id="cafeAdd"
          placeholder="카페 주소를 입력하세요"
        ></input>

        <h2 className="inputTitle"> 사업자 번호 </h2>
        <input
          className="inputBox"
          name="businessNum"
          id="businessNum"
          placeholder="사업자번호를 입력하세요"
        ></input>
        <br></br>
        <button className="inputbutton" type="submit">
          {" "}
          회원가입{" "}
        </button>
      </div>
    </div>
  );
};
