import "./LoginPage.css";
import * as React from "react";
import { useState, useNavigate } from "react";
import axios from "axios";
import { Link, redirect } from "react-router-dom";

export const LoginPageBusiness = (props) => {
  const [inputId, setInputId] = useState("");
  const [inputPw, setInputPw] = useState("");

  const handleInputId = (e) => {
    setInputId(e.target.value);
  };

  const handleInputPw = (e) => {
    setInputPw(e.target.value);
  };

  const onClickLogin = () => {
    console.log("click login");
  };

  return (
    <div className="Login_backGroundSet">
      <div className="Login_Container">
        <div className="Login_Title">
          <Link to="/LoginPageBusiness">
            <img src={require("./camomileBusiness.png").default}></img>
          </Link>
        </div>
        <form action="http://127.0.0.1:3001/LoginPage" method="post">
          <div className="Login_Row">
            <label htmlFor="input_id"></label>
            <img
              src={require("./person_FILL0_wght400_GRAD0_opsz24.png").default}
            ></img>
            <input
              type="text"
              className="Login_input_id"
              value={inputId}
              placeholder="아이디"
              onChange={handleInputId}
            />
          </div>
          <div className="Login_Row">
            <label htmlFor="input_pw"></label>
            <img
              src={require("./lock_FILL0_wght400_GRAD0_opsz24.png").default}
            ></img>
            <input
              type="password"
              className="Login_input_pw"
              value={inputPw}
              placeholder="패스워드"
              onChange={handleInputPw}
            />
          </div>
          <div className="Login_chbx">
            <input type="checkbox"></input>
            로그인 상태 유지
          </div>
          <div>
            <button
              type="submit"
              value="LogIn"
              className="Login_button"
              onClick={onClickLogin}
            >
              로그인
            </button>
          </div>
        </form>
        <div>
          <hr className="border"></hr>
        </div>
      </div>

      <br></br>
      <br></br>
      <br></br>
      <br></br>

      <div>
        <h5 className="Login_signup">아직 계정이 없으신가요?</h5>
        <button type="button" className="Login_joinbutton">
          회원가입
        </button>
      </div>
      {/* <div className="container">
        <div className="col-md-10 col-md-offset-1 section-title">
          <h2>기철홀릭</h2>
        </div>
        <div className="row">
          {props.data
            ? props.data.map((d, i) => (
                <div key={`${d.title}-${i}`} className="col-xs-6 col-md-3">
                  {" "}
                  <div><img className={d.icon}/></div>
                  <h3>{d.title}</h3>
                  <p>{d.text}</p>
                </div>
              ))
            : "Loading..."}
        </div>
      </div> */}
    </div>
  );
};
