import "./join.css";

export const JoinMember = (props) => {
  return (
    <form action="http://127.0.0.1:3000/joinMember">
      <div className="join">
        <div className="joinInput">
          <img
            className="img"
            src={require("./camomile.png").default}
            style={{ marginBottom: "3%" }}
          ></img>
          <h2 className="inputTitle"> 아이디 </h2>
          <input
            className="inputBox"
            id="id"
            placeholder="아이디를 입력하세요"
          ></input>

          <h2 className="inputTitle"> 비밀번호 </h2>
          <input
            className="inputBox"
            id="pw"
            placeholder="비밀번호를 입력하세요"
            type={"password"}
          ></input>
          <h2 className="inputTitle"> 비밀번호 확인 </h2>
          <input
            className="inputBox"
            id="pwConfirm"
            placeholder="비밀번호를 확인하세요"
            type={"password"}
          ></input>

          <h2 className="inputTitle"> 이름 </h2>
          <input
            className="inputBox"
            id="name"
            placeholder="이름을 입력해주세요"
          ></input>

          <h2 className="inputTitle"> 생년월일 </h2>
          <input
            className="inputBox"
            id="birth"
            type="text"
            maxLength={8}
            placeholder="YYYY-MM-DD"
          ></input>

          <h2 className="inputTitle"> 휴대전화 </h2>
          <input
            className="inputBox"
            name="phone"
            id="phone"
            placeholder="전화번호를 입력해주세요"
          ></input>

          <h2 className="inputTitle"> 닉네임 </h2>
          <input
            className="inputBox"
            name="nick"
            id="nick"
            placeholder="닉네임을 입력하세요"
          ></input>
          <br></br>
          <button className="inputbutton" type="submit">
            {" "}
            회원가입{" "}
          </button>
        </div>
      </div>
    </form>
  );
};
